    transform: translate3d(-3%, -6%, 0) rotate(-5deg);
  }
  90% {
    transform: translate3d(0.5%, 1%, 0) rotate(1deg);
  }
  to {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
