    transform: translate3d(1.5%, -2.5%, 0) rotate(3deg);
  }
  91% {
    transform: translate3d(-0.5%, 1%, 0) rotate(0deg);
  }
  to {
    transform: translate3d(0, 0, 0) rotate(0deg);
    opacity: 1;
  }
