.social-block__link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.social-block__link--fb svg {
  width: 0.8rem;
  height: 1.7rem;
}
@media (min-width: 2000px) {
  .social-block__link--fb svg {
    width: 8px;
    height: 17px;
  }
}
.social-block__link--insta svg {
  width: 1.5rem;
  height: 1.5rem;
}
@media (min-width: 2000px) {
  .social-block__link--insta svg {
    width: 15px;
    height: 15px;
  }
}
.social-block__link--vk svg {
  width: 1.7rem;
  height: 1rem;
}
@media (min-width: 2000px) {
  .social-block__link--vk svg {
    width: 10px;
    height: 10px;
  }
}

